___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --sidebar-bg-color: #f0f0f0;
  --main-bg-color: #fffbff;
  --input-border-color: rgba(0, 0, 0, 0.53);
  --button-bg-color: #d1d0d0;
  --button-hover-bg-color: #c4c1c1;
}

body {
  font-family: system-ui, sans-serif;
  min-height: 100dvh;
  display: grid;
  grid-template-columns: 250px 1fr;
}

.dialog {
  border-radius: 12px;
  padding: 24px;
  place-self: center;
}

.dialog > div {
  display: flex;
  justify-content: space-between;
}

.dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.2);
}

.button {
  border-radius: 99999px;
  border: none;
  padding: 4px 16px;
  cursor: pointer;
  background-color: var(--button-bg-color);
}

.button:hover {
  transition: all 0.1s ease-in-out;
  background-color: var(--button-hover-bg-color);
}

.form {
  display: grid;
  justify-items: start;
  gap: 12px;
  margin-top: 16px;
}

.form__label {
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.form__input,
.form__select {
  display: block;
}

.form__input,
.form__select {
  border-radius: 12px;
  padding: 2px 10px;
  border: 1px solid var(--input-border-color);
}

.form__select {
  background-color: transparent;
}

.form__button {
  margin-top: 12px;
}

.aside,
.main {
  padding: 16px;
}

.aside {
  background-color: var(--sidebar-bg-color);
  z-index: 10;
  box-shadow: 3px 0px 5px rgba(0, 0, 0, 0.233);
}

.main {
  background-color: var(--main-bg-color);
}

.projects {
  margin-top: 24px;
}

.projects div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.list {
  padding: 0;
  display: grid;
  gap: 8px;
}

.list__item {
  list-style: none;
  display: flex;
  gap: 8px;
}

.list__item:hover .nav__button--delete {
  display: block;
}

.nav__button {
  width: 100%;
  text-align: left;
}

.nav__button--delete {
  display: none;
  width: auto;
}

.main > div {
  max-width: 1280px;
  margin-inline: auto;
}

.main__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.todo-list {
  margin-top: 36px;
  display: grid;
  gap: 24px;
}

.todo-list__item {
  cursor: pointer;
  padding: 12px;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.486);
  border-radius: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.todo-list__item--priority-low {
  border-left: 6px solid green;
}

.todo-list__item--priority-medium {
  border-left: 6px solid yellow;
}

.todo-list__item--priority-high {
  border-left: 6px solid red;
}

.todo-list__item:hover .todo-list__details {
  opacity: 1;
  transition: opacity 0.15s ease-in-out;
}

.todo-list__content {
  display: flex;
  gap: 16px;
  align-items: center;
  min-width: 40%;
}

.todo-list__content > p {
  margin-inline: auto;
}

.todo-list__details {
  width: 60%;
  opacity: 0;
  margin-left: 32px;
  display: grid;
  grid-template-columns: 3fr 1fr;
  align-items: center;
  gap: 32px;
  justify-items: center;
}

.todo-list__buttons {
  display: flex;
  gap: 12px;
}
